@import url(https://fonts.googleapis.com/css?family=Sulphur+Point&display=swap);

html {
  box-sizing: border-box;
}
*, *:before, *:after {
  box-sizing: inherit;
}

body {
  background: #2F3E4F;;
}

.App {
  --color-text: #8b8b8b;
  --color-bg:  #2F3E4F;;
  --color-link-hover: #f1f1f1;
  background: var(--color-bg);
  margin: 0;
  padding: 0;
  overflow: hidden;
  font-family: 'Sulphur Point', sans-serif;
  font-size: 0.9rem;
  color: var(--color-text);
}

@media screen and (min-width: 601px) {
  h3 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: left;
    position: absolute;
    width: 40%;
    left: 50%;
    top: 50%;
    font-size: 1.4em;
    transform: translate(-50%, -50%);
  }
} 

@media screen and (max-width: 600px) {
  h3 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: left;
    position: absolute;
    width: 30%;
    left: 50%;
    top: 50%;
    font-size: 10px;
    transform: translate(-50%, -50%);
  }
}

@media screen and (min-width: 601px) {
  h2 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 1.4em;
    transform: translate(-50%, -50%);
  }
}  

@media screen and (max-width: 600px) {
  h2 {
    letter-spacing: 0.1em;
    line-height: 1.9em;
    text-align: center;
    position: absolute;
    left: 50%;
    top: 50%;
    font-size: 10px;
    transform: translate(-50%, -50%);
  }
} 

.Sidebar {
  --color-text: #8b8b8b;
  --color-link-hover: #bdbdbd;
  background: none;
  /* height: 100%; */
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  overflow-x: hidden;
  padding-top: 20px;
  padding-left: 20px;
  font-family: 'Sulphur Point', sans-serif;
}

/* Style the sidenav links and the dropdown button */
@media screen and (min-width: 601px) {
  .Sidebar a {
    padding: 6px 6px 6px 16px;
    text-decoration: none;
    font-size: 20px;
    color: var(--color-text);
    display: block;
    border: none;
    background: none;
    /* width: 100%; */
    text-align: left;
    cursor: pointer;
    outline: none;
    }
}

@media screen and (max-width: 600px) {
  .Sidebar a {
    padding: 6px 6px 6px 16px;
    text-decoration: none;
    font-size: 10px;
    line-height: 0.1em;
    color: var(--color-text);
    display: block;
    border: none;
    background: none;
    /* width: 100%; */
    text-align: left;
    cursor: pointer;
    outline: none;
    }
  }

/* On mouse-over */
.Sidebar a:hover {
  color: var(--color-link-hover);
}

